<template>
    <div>
      <div class="function m-30">
        <div class="desc-box">
            <div class="title">功能介绍</div>
            <div class="sub-title m-10">FUNCTION INTRODUCTION</div>
        </div>
        <div class="f-tac f-cb m-30 func-box">
          <div class="func-pic">
            <div class="icon-box">
              <img src="../assets/icon5.png" alt="">
              <p>数据管理</p>
            </div>
            <div class="desc-box">
              <p>投顾资料维护</p>
              <p>投顾尽职调查</p>
              <p>投顾调查问卷</p>
              <p>投顾基金评测</p>
              <p>投顾评分准入分类</p>
              <p>投顾组合构建&模拟</p>
            </div>
          </div>
          <div class="func-pic">
            <div class="icon-box">
              <img src="../assets/icon6.png" alt="">
              <p>产品管理</p>
            </div>
            <div class="desc-box">
              <p>公司运营</p>
              <p>产品维护</p>
              <p>产品份额登记(TA)</p>
              <p>产品估值(FA)</p>
              <p>产品数据采集</p>
              <p>产品披露和客户维护</p>
            </div>
          </div>
          <div class="func-pic">
            <div class="icon-box">
              <img src="../assets/icon7.png" alt="">
              <p>交易</p>
            </div>
            <div class="desc-box">
              <p>盘后风控</p>
              <p>合规风控</p>
              <p>净值风控</p>
              <p>风险风控</p>
              <p>产品联合风控</p>
            </div>
          </div>
          <div class="func-pic">
            <div class="icon-box">
              <img src="../assets/icon8.png" alt="">
              <p>投后管理</p>
            </div>
            <div class="desc-box">
              <p>产品绩效</p>
              <p>绩效归因</p>
              <p>风险披露</p>
              <p>报告报表</p>
            </div>
          </div>
        </div>
      </div>
      <div class="superiority m-30">
        <div class="desc-box">
            <div class="title">产品优势</div>
            <div class="sub-title m-10">PRODUCT SUPERIORITY</div>
        </div>
        <div class="su-box-wrapper f-cb">
            <div class="su-box">
              <el-container class="su-pic">
                  <el-header height="160px" >
                    <img src="../assets/su5.png" alt="">
                    <h4>业务落地</h4>
                  </el-header>
                  <el-main>
                        <p class="grey" >以机构客户为中心精确匹配目标客户需求</p>
                        <p class="grey" >功能和需求和客户共同确定</p>
                        <p class="grey" >客户实盘交易稳定运行超过1年</p>
                  </el-main>
              </el-container>
            </div>
            <div class="su-box">
              <el-container class="su-pic">
                  <el-header height="160px" >
                    <img src="../assets/su6.png" alt="">
                    <h4>技术深耕</h4>
                  </el-header>
                  <el-main>
                        <p class="grey" >交易低延迟</p>
                        <p class="grey" >分布式处理能力</p>
                        <p class="grey" >系统容错稳定性强</p>
                  </el-main>
              </el-container>
            </div>
            <div class="su-box">
              <el-container class="su-pic">
                  <el-header height="160px" >
                    <img src="../assets/su7.png" alt="">
                    <h4>服务专业</h4>
                  </el-header>
                  <el-main>
                        <p class="grey" >快速响应客户需求</p>
                        <p class="grey" >专业运维监测系统运行</p>
                        <p class="grey" >支持客户定制开发功能</p>
                  </el-main>
              </el-container>
            </div>
        </div>
      </div>
    </div>
</template>

<script>
export default {
  beforeRouteEnter: (to, from, next) => {
    next(vm => {
      let $tri = document.querySelector(".triangle");
      $tri.classList.add("trade");
    });
  }
};
</script>

<style scoped>
.func-box {
  perspective: 200px;
}
.func-pic {
  position: relative;
  display: inline-block;
  width: 250px;
  height: 350px;
  overflow: hidden;
  transition: all 0.3s;
  perspective: 800px;
  /* background-color: #c1ccce1a; */
}
.func-pic:hover {
  box-shadow: 1px 1px 20px 1px #d6d6d6;
  transform: translate3D(0, 0, 10px);
}
.func-pic .icon-box img {
  transition: all 0.3s;
}
.func-pic:hover .icon-box img {
  transform: translateZ(50px);
}
.icon-box {
  padding-top: 40px;
}
.icon-box p {
  padding: 10px;
  font-weight: bold;
  font-size: 18px;
}
.desc-box p {
  font-size: 14px;
  color: #999;
  height: 24px;
}
.su-box-wrapper {
  text-align: center;
  margin: 0 auto;
}
.su-box {
  display: inline-block;
  width: 340px;
  margin-left: 34px;
  border-right: 1px solid #d7d7d7;
  text-align: left;
}
.su-box:last-of-type {
  border: none;
}
.su-pic .el-header {
  /* overflow: hidden; */
  position: relative;
}
.su-pic img {
  transition: all 0.3s;
}
.su-pic:hover img {
  transform: scale(1.04);
}
.su-text {
  padding: 18px;
}
.su-text p {
  font-size: 14px;
}
.su-box p {
  font-size: 14px;
  height: 24px;
}
.su-box .el-main {
  margin-top: 20px;
}
.su-box h4 {
  color: #fff;
  top: 50%;
  left: 50%;
  position: absolute;
  transform: translate(-75%, -50%);
}

</style>



