<template>
  <div>
      <div class="f-tac m-30">
        <div class="desc-box">
            <div class="title">行业痛点</div>
            <div class="sub-title m-10">INDUSTRY PAIN-POINT</div>
        </div>
        <div class="f-tac f-cb m-30 slider-wrapper">
            <div class="slider-box">
                <swiper :options="swiperOption" >
                    <swiper-slide class="pic pic1" >
                    </swiper-slide>
                    <swiper-slide class="pic pic2" >
                    </swiper-slide>
                    <swiper-slide class="pic pic3" >
                    </swiper-slide>
                    <swiper-slide class="pic pic4" >
                    </swiper-slide>
                    <div class="swiper-pagination" slot="pagination"></div>
                </swiper>
            </div>
        </div>
      </div>
  </div>
</template>

<script>
import "swiper/dist/css/swiper.css";
import { swiper, swiperSlide } from "vue-awesome-swiper";
export default {
  data() {
    return {
        swiperOption: {
            direction: 'vertical',
            pagination: {
                el: '.swiper-pagination',
                clickable: true
            },
            autoplay: true,
            loop: true
        }
    };
  },
  components: {
    swiper,
    swiperSlide
  }
};
</script>

<style scoped >
.slider-wrapper {
  position: relative;
  border: 1px solid #d7d7d7;
  width: 1000px;
  height: 400px;
  margin: 0 auto;
  margin-top: 30px;
}
.slider-box {
  position: absolute;
  width: 500px;
  height: 320px;
  left: -30px;
  top: 40px;
  overflow: hidden;
}
.swiper-slide {
    height: 320px;
}
.swiper-container {
    position: unset;
}
.pic {
  background: url(../assets/pain1.png) no-repeat left top;
  height: 320px;
}
.pic1 {
}
</style>



