<template>
  <div class="productions" >
      <div class="p-banner">
        <div class="tab-row">
          <div class="tab-toggle">
            <router-link to="/productions" class="tab-title" event="mouseover" tag="li" exact>
              <a>FOF综合信息管理平台</a>
            </router-link>
            <router-link to="/productions/trade" class="tab-title" event="mouseover" tag="li" exact>
              <a>量化交易平台</a>
            </router-link>
          </div>
          <div class="triangle"></div>
        </div>
      </div>
      <transition :name="transitionName" mode="out-in" >
        <router-view></router-view>
      </transition>
  </div>
</template>

<script>
export default {
    data() {
        return {
            transitionName: ''
        }
    },
    watch: {
        '$route'(to, from) {
            const toDepth = to.path.split('/').length
            const fromDepth = from.path.split('/').length
            this.transitionName = toDepth < fromDepth ? 'slide-right' : 'slide-left'
        }
    },
    beforeRouteUpdate: (to, from, next) => {
        let $tri = document.querySelector(".triangle");
        $tri.classList.remove("trade");
        next();
    }
};
</script>

<style scoped >
.p-banner {
  width: 100%;
  height: 600px;
  background: url(../assets/p-banner.png) no-repeat center top;
  position: relative;
}
.tab-row {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 60px;
  background: #0000004d;
  line-height: 60px;
}
.tab-row .tab-toggle {
  width: 50%;
  margin: 0 auto;
}
.tab-title {
  width: 50%;
  float: left;
  text-align: center;
  list-style: none;
}
.tab-title a {
  color: #ffffff8a;
  text-decoration: none;
  transition: all 0.3s;
}
.tab-title.router-link-active a {
  color: #fff;
}
.triangle {
  position: absolute;
  background: #fff;
  width: 16px;
  height: 16px;
  bottom: -10px;
  left: 37%;
  transform: rotate(45deg);
  transition: left .2s;
}
.triangle.trade {
  left: 62%;
}
</style>



