<template>
    <div id="app">
        <el-container>
            <el-header class="header">
                <el-row type="flex" justify="space-between" >
                  <el-col :span="4" :offset="1">
                      <div class="logo">
                          <img src="./assets/logo-white.png" alt="">
                      </div>
                  </el-col>
                  <el-col :xl="8" :lg="10" :md="12" :sm="14" :xs="16">
                      <div class="nav">
                          <router-link to="/" >首页</router-link>
                          <router-link to="/productions" >产品</router-link>
                          <router-link to="/solutions">解决方案</router-link>
                          <a @click="scroll(document.querySelector('.cooperation'))">合作与支持</a>
                          <a href="#">关于我们</a>
                      </div>
                  </el-col>
                </el-row>
            </el-header>
            <el-main>
                <router-view/>
            </el-main>
            <el-footer>
                <div class="footer">
                    成都高御信息科技有限公司
                </div>
            </el-footer>
        </el-container>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                document
            }
        },
        methods: {
            scroll(ele) {
                ele.scrollIntoView(true);
            }
        }
    }
</script>

<style>
body {
    margin: 0;
}
.header {
    position: absolute;
    width: 100%;
    z-index: 3;
}
.logo, .nav {
    line-height: 60px;
}
.logo img {
    vertical-align: middle;
}
.nav a {
    padding-left: 36px;
    text-decoration: none;
    color: #ffffffc9;
}
.nav a:hover {
    color: #ffffff;
}
.footer {
    text-align: center;
    margin-top: 20px;
    height: 60px;
    line-height: 60px;
    background: #0E3D84;
    color: #fff;
}
</style>



