import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Productions from './views/Productions.vue'
import Solutions from './views/Solutions.vue'
import Fof from './views/Fof.vue'
import Trade from './views/Trade.vue'
import Funding from './views/Funding.vue'
import Org from './views/Org.vue'

Vue.use(Router)

export default new Router({
  routes: [
        {
            path: '/',
            name: 'home',
            component: Home
        },
        {
            path: '/productions',
            component: Productions,
            children: [
                {
                    path: '',
                    component: Fof
                },
                {
                    path: 'trade',
                    component: Trade
                }
            ]
        },
        {
            path: '/solutions',
            component: Solutions,
            children: [
                {
                    path: '',
                    component: Funding
                },
                {
                    path: 'org',
                    component: Org
                }
            ]
        }
    ]
})



